.item-label{
  color: white;
}

.save{
  background-color: rgb(47, 128, 237);
  color: white;
  padding: 5px 15px;
  border: transparent;
  border-radius: 3px;
  width: 70px;
  margin: 5px 0 0 5px;
}

.back{
  background-color: rgb(47, 128, 237);
  color: white;
  margin: 5px 0 0 5px;
  padding: 5px 15px;
  border: transparent;
  border-radius: 3px;
  width: 70px;
}

.portfolio-table{
  .table thead tr{
    color: white;
  }

  .table tbody tr{
    color: white;
  }
}

.my-portfolio-header{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;
  position: relative;
  width: 100%;
  background: rgb(21, 27, 36);
  padding: 32px;
  .header-box{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 0;
    overflow: hidden;

    .back-style{
      position: absolute;
      left: 0px;
      top: 0px;
      width: 101%;
      height: 100%;
      mix-blend-mode: normal;
      z-index: 1;
      background: linear-gradient(rgba(40, 50, 62, 0.29) 0%, rgba(40, 52, 62, 0.55) 49.17%, rgba(40, 50, 62, 0.85) 100%);
    }
  }

  .header-info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: left;
    flex: 1 1 auto !important;

    .breadcrumb {
      background-color: transparent;

      li {
        padding: 5px;
        color: white;

        svg {
          font-size: 12px;
        }

        &:last-child svg {
          display: none;
        }
      }
    }

    .btn-new-portfolio{
      margin-top: 20px;
      position: relative;
      user-select: none;
      vertical-align: top;
      font-size: 18px;
      height: 40px;
      color: white;
      display: inline-block;
      width: auto;
      box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px;
      background-color: rgb(13, 110, 241);
      transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
      border-width: 0px;
      border-style: initial;
      border-color: initial;
      border-image: initial;
      border-radius: 2px;
      overflow: hidden;
      padding: 0px 16px;
      text-decoration: none;
    }
  }
}

.my-portfolio-card{
  margin-top: 15px;
  position: relative;
  z-index: 0;
  background-color: rgb(21, 27, 36);
  overflow: hidden;
  //margin-right: -15px;
  //margin-left: -15px;
  .portfolio-card-img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: blur(0rem);
    opacity: 1;
    transition: all 0.4s ease-out 0s;
  }

  .my-portfolio-card-info{
    height: 152px;
    padding: 1.6rem;
    color: white;
    text-align: center;
  }
}

.actions{
  position: relative;
    user-select: none;
    vertical-align: top;
    font-size: 14px;
    margin-bottom: 4px;
    height: 40px;
    color: white;
    display: inline-block;
    width: auto;
    box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px;
    background-color: rgb(13, 110, 241);
    transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
    border-width: 0px;
    border-style: initial;
    border-color: initial;
    border-image: initial;
    border-radius: 2px;
    overflow: hidden;
    padding: 0px 16px;
    text-decoration: none;
}

.report-box{
  height: 120px;
  background-color: rgb(38, 46, 58);
  padding: 16px;
  transition: background-color 0.2s ease 0s;
  margin-left: 15px;
}

.unit{
  line-height: 1.5;
    color: rgb(255, 255, 255);
    margin-bottom: -2px;
    font-size: 13px;
    span{
      opacity: 0.5;
    margin-right: 4px;
    font-size: 11px;
    }
}

.co-name{
  font-size: 14px;
  line-height: 1.5;
    font-weight: 500;
    color: rgb(255, 255, 255);
    padding-left: 24px;
    width: 100%;
    margin-left: 10px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    text-align: right;
}